<template>
  <div class="index">
    <HeadTop :title="$t('index.投资')" :isShowTop="true" :isSubHeader="false"></HeadTop>
    <div class="floor-top">
      <div class="nums flex-middle">
          <div class="item border-1px-r" @click="$router.push({name:'RecordLpPower'})">
            <div class="key">{{$t('index.已投资')}}(ELE)</div>
            <div class="value">{{Number(investAmount/10**18).toFixed(2)}}</div>
          </div>
          <div class="item">
            <div class="key">{{$t('index.累计领取')}}(ELE)</div>
            <div class="value">{{Number(totalBack/10**18).toFixed(2)}}</div>
          </div>
           <div class="item border-1px-r" @click="$router.push({name:'RecordLpPower'})">
            <div class="key">{{$t('index.投资收益')}}(ELE)</div>
            <div class="value">{{Number(investIncome/10**18).toFixed(2)}}</div>
          </div>
          <div class="item">
            <div class="key">{{$t('index.推荐收益')}}(ELE)</div>
            <div class="value">{{Number(introIncome/10**18).toFixed(2)}}</div>
          </div>
        </div>
      <div class="nums flex-middle">
          <div class="item">
            <div class="key">{{$t('index.待领取收益')}}(ELE)</div>
            <div class="value">{{Number(reward/10**18).toFixed(5)}}</div>
          </div>
            <div class="item">
              <span class="btn" @click="withdraw">{{$t('index.领取')}}</span>
          </div>
        </div>  
    </div>
    <div class="list-btn flex-middle">
      <van-button type="primary" size="large" @click="$router.push({name: 'Order'})">{{$t('index.投资记录')}}</van-button>
      <van-button type="primary" style="margin:0 6px" size="large" @click="$router.push({name: 'WithdrawList'})">{{$t('index.领取记录')}}</van-button>
      <van-button type="primary" size="large" @click="$router.push({name: 'IntroIncomeList'})">{{$t('index.推荐收益记录')}}</van-button>
    </div>
    <div class="swap">
     <div class="swap-item">
       <div class="title">{{$t('index.选择投资类型')}}</div>
      <div class="choose-type flex-between">
        <div class="type-item" v-for="item in types" :class="typeId == item.id ? 'active' : ''" @click="selectType(item)">
          <img :src="require('@/assets/images/jb_'+ item.id +'.png')" alt="">
          <span>{{item.needUsdtAmount/10**18}}USDT</span>
        </div>
      </div>
      <div class="price-desc">{{$t('index.需要')}}<span>{{Number(needEleAmount/10**18).toFixed(4)}}</span>ELE</div>
      <div class="button-box">
        <van-button type="primary" :loading="btn.swaploading" size="large" :loading-text="$t('index.投资')+ '...'" block v-if="Number(allowance) > 0" @click="invest">{{$t('index.投资')}}</van-button>
        <van-button type="primary" :loading="btn.swaploading" v-else size="large" :loading-text="$t('index.正在授权')+ '...'" @click="approve">{{$t('index.授权')}}ELE</van-button>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import { Toast } from 'vant';
import { MaxUint256, AddressZero } from "@ethersproject/constants";
import { intro } from '@/storage'
import { getUserInfo } from '@/api'
import idoAbi from "@/utils/idoAbi";
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      intro: intro.get() || config.rootAddress,
      eleContract: null,
      investContract: null,
      balance: 0,
      types: [],
      typeId: '',
      type: null,
      allowance: '',
      investAmount: '0.00',
      totalBack: '0.00',
      needEleAmount: '0.00',
      investIncome: '0.00',
      introIncome: '0.00',
      reward: '0.00',
      btn: {
        swaploading: false
      },
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.eleContract = new data.web3.eth.Contract(tokenAbi, config.ELEAddress)
        data.idoContract = new data.web3.eth.Contract(idoAbi, config.idoAddress)
        data.initData()
      },
      initData: () => {
        data.getAllStakeType()
        data.getBalance()
        data.getAllowance()
        data.getInfo()
        data.getUserInfo()
      },
      selectType: async (type) => {
        data.type = type
        data.typeId = type.id
        await data.getNeedEleAmount()
      },
      getUserInfo: () => {
        getUserInfo({user: data.address}).then((res) => {
          data.investIncome = res.data.userInfo.haveBack
          data.reward = res.data.userInfo.reward
          data.introIncome = res.data.userInfo.introIncome
          console.log(data.investIncome,data.reward,data.introIncome)
        })
      },
      getInfo: async () => {
         let userInfo = await data.investContract.methods.userInfo(data.address).call()
         data.investAmount = userInfo.investAmmount
         data.totalBack = userInfo.totalBack
      },
      getBalance: async () => {
        // let decimals = await data.eleContract.methods.decimals().call()
        let balance = await data.eleContract.methods.balanceOf(data.address).call()
        data.balance =  balance
      },
      getAllowance: async () => {
        let allowance = await data.eleContract.methods.allowance(data.address, config.investAddress).call()
        data.allowance = allowance;
      },
      getAllStakeType: async () => {
        let types = await data.investContract.methods.getAllStakeType().call()
        console.log(types)
        data.types = types
        data.typeId = types[0].id
        data.type = types[0]
        await data.getNeedEleAmount()
      },
      getNeedEleAmount: async () => {
        let eleAmount = await data.investContract.methods.getEleAmount(data.type.needUsdtAmount).call()
        data.needEleAmount = eleAmount;
      },
      approve: async () => {
        data.btn.swaploading = true
        try {
          await data.eleContract.methods.approve(config.investAddress, MaxUint256).send({from: data.address})
          Toast(t('index.授权成功'))
          data.getAllowance()
          data.btn.swaploading = false
        } catch (error) {
          Toast(t('index.授权失败'))
          data.btn.swaploading = false
        }   
      },
      withdraw: async () => {
      if(Number(data.reward) <= 0) {
        Toast('暂无收益可领')
        return
      }
       try {
         let value = await data.investContract.methods.feeValue().call()
         await data.investContract.methods.withdraw().send({from: data.address, value: value})
         Toast(t('index.领取成功'))
       } catch (error) {
         Toast(t('index.领取失败'))
       }
        
      },
      invest: async () => {
        if(Number(data.needEleAmount) > Number(data.balance)) {
          Toast(t('index.ELE余额不足'))
          return
        }
        let introUserInfo = await data.investContract.methods.userInfo(data.intro).call()
        let userUserInfo = await data.investContract.methods.userInfo(data.address).call()
        let userInfo = await data.idoContract.methods.userInfo(data.address).call()
        if(userInfo.intro != AddressZero) {
          data.intro = userInfo.intro;
        } else {
           if(introUserInfo.intro == AddressZero && data.intro != config.rootAddress && userUserInfo.intro == AddressZero) {
          Toast(t('index.推荐人无效'))
          return
        }
        }
       
         data.btn.swaploading = true
          try {
           
          let gas = await data.investContract.methods.stake(data.typeId, data.intro).estimateGas({from: data.address})
          console.log(gas)
          await data.investContract.methods.stake(data.typeId, data.intro).send({from: data.address})
          Toast(t('index.投资成功'))
           data.initData()
          data.btn.swaploading = false
        } catch (error) {
          Toast(t('index投资失败'))
          data.btn.swaploading = false
        }
      }
    })
  
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData,
    };
  }
}
</script>
<style lang="less" scoped>
.list-btn{
  margin-bottom: 20px;
  padding: 0 16px;
}
.index {
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.swap{
  padding: 0 16px;
}
.floor-top {
  flex-shrink: 0;
  padding: 20px 16px 0;
  word-break: break-all;
 .nums {
      position: relative;
      z-index: 100;
      border-radius: 6px;
      background: rgba(255, 255, 255, 0.15);
      padding: 15px 13px;
      margin-bottom: 15px;
      flex-wrap: wrap;
      .item {
        width: 50%;
        margin: 5px 0;
        text-align: center;
        .btn{
          background: #65FF00;
          color: rgba(0, 0, 0, 0.75);
          padding: 6px 12px;
          border-radius: 6px;
        }
      }
      .key {
        color: @tip-color;
        font-size: 12px;
        padding-bottom: 4px;
      }
      .value {
        font-size: 20px;
        color: @deep-color;
        font-weight: 700;
      }
    }
}
.swap-item{
  border-radius: 5px;
  margin-bottom: 15px;
  .title{
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
  }
  .price-desc{
    font-size: 15px;
    color: #fff;
    margin: 15px 8px;
    span{
      color: #FFD511;
    }
  }
}
.choose-type{
  margin-bottom: 10px;
  .type-item{
    width: 31%;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 8px;
     background: rgba(255, 255, 255, 0.15);
     border-radius: 5px;
     &.active{
       border: 1px solid rgba(101, 255, 0, 0.4);
     }
    img{
      width: 60px;
    }
    span{
      color: #fff;
      margin-top: 5px;
    }
  }
}
</style>
